export default{
	
	data(){
		return{
			theMid:'',
			moveLineOffset: 100/12,
			currentIndex: 0,
			showNewsData: [
				{
					title:'最后4小时！最后4小时！最后4小时！最后4小时！',
					content:'重复关注会员：昵称为【开心就好】的朋友，在2020-06-15 10:36:03已经关注成为【惠农6:03已经关公',
					time:1593571037
				},
				{
					title:'会员通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'订单通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'我的评价',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				}
			],
			tabList: [{
				id: 0,
				name: '会员通知'
			}, {
				id: 1,
				name: '订单通知'
			}, {
				id: 2,
				name: '系统通知'
			}],
			page:1,
			loadingText: '正在加载数据...',
		}
	},
	onReachBottom() {
	    this.page++;
	    this.loadingText='正在加载更多数据...';
	    this.getServicList();
	},
	onPullDownRefresh() {
		this.getServicList();
	},
	onLoad(param) {
		if(param.query){
			this.changeIndex(param.query);
		}
	},
	methods:{
		clickItem(e,index){
			this.showNewsData=[];
			this.page=1;
			this.changeIndex(e.id);
		},
		
		changeIndex(index){
			var _this = this;
			_this.currentIndex = index;
			_this.moveLineOffset=(index*33.3333)+100/12;
			this.loadingText='正在加载数据...';
			
			uni.startPullDownRefresh({
				
			})
		},
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		
		clickSend(){
		    uni.navigateTo({
		    	url:'sendNotice'
		    })	
		},
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
